.ProfileCard{
    border-radius: 1.5rem;
    display: flex;
    flex-direction: column;
    position: relative;
    gap: 1rem;
    overflow-x: clip;
    background: var(--cardColor);
}

.ProfileImages{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center; 
}

.ProfileImages img:nth-of-type(1){
    width: 100%;
}


.ProfileImages img:nth-of-type(2){
    width: 6rem;
    border-radius: 50%;
    position: absolute;
    bottom: -3rem;
    box-shadow: var(--profileShadow);
}

.profileImage2
{
    width: 6rem;
    border-radius: 50%;
    position: absolute;
    bottom: -3rem;
    box-shadow: var(--profileShadow);
}

.ProfileNames{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3rem;
    gap: 10px;
}  

.ProfileNames span:nth-of-type(1){
    font-weight:bold;
}


/* follow status*/
hr{
   width: 85%;
   border: 1px solid var(--hrColor);

}
.followstatus{
    display: flex;
    /*flex-direction: column;*/
    align-items: center;
    justify-content: center;
    gap: 0.35rem;
}


.followstatus div{
    display: flex;
    /*gap: 1rem;*/
    width: 100%;
    align-items: center;
    justify-content: center;
}

.follow{
    display: flex;
    flex-direction: column;
    gap: 0.4rem;
    align-items: center;
    justify-content: center;
}

.follow span:nth-of-type(1){
    font-weight: bold;
}

.follow span:nth-of-type(2){
    color: var(--gray);
    font-size: 13px;
}

.v1{
    width:60%;
    border-right: 2px solid var(--hrColor);
    transform: rotate(90deg);

}


.ProfileCard>span{
    font-weight: bold;
    color: orange;
    align-self: center;
    margin-bottom: 1rem;
    cursor: pointer;
}