.Auth{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    gap: 4rem;
    position: relative;
}

.Auth>div{
    display: flex;
    align-items: center;
    justify-content: center; 
}

/* left Side */
.a-left{
    gap: 2rem;
}

.a-left>img{
    width: 4rem;
    height: 4rem;
}

.webname>h1{
    font-size: 3rem;
    background-color: red;
    /* create the gradient*/
    background-image: var(--buttonBg);

    background-size: 100%;
    background-repeat: repeat;

    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;


}


.webname>h6{
    font-size: 0.85rem;

}


.infoFrom{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.infoInput{
    border: none;
    outline: none;
    background-color: var(--inputColor);
    border-radius: 8px;
    padding: 20px;
    flex: 1;
}

.infoFrom>div{
    display: flex;
    gap: 1rem;
    height: 2rem;
    width: 90%;

}


.infobutton{
width: 6rem;
height: 2rem;
align-self: flex-end;
}

.authForm{
    background-color: var(--cardColor);
    border-radius: 1rem;
    padding: 1rem;
}

.authForm>div{
    width: 100%;
    align-items: center;
    justify-content: center;

}