.LogoSearch{
    display: flex;
    gap: 0.75rem;
}

.Search{
    display: flex;
    background-color:var(--inputColor);
    border-radius: 5px;
    padding: 5px;
}

.Search>input{
    background-color: transparent;
    border: none;
    outline:none;
}

.s-icon{
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(106.23deg, #f99827,#f95f35 100%);
    border-radius:5px ;
    padding:4px ;
    color: white;
}

.s-icon:hover{
    cursor: pointer;
}